import myImage from '../../image/cartoonized.jpg';
import styles from './Welcome.module.css';


const Welcome =()=>{
    return (
      <div className={styles['welcome-container']}>
        <h1> UI Website</h1>
        <img src={myImage} alt="welcome" ></img>
        <section>
          Hello,<br/> I am a ReactJs software developer, and the following website sections break down some of my idea about ReactJs components for the UI web/app. Currently, 
          there is just some basic example, but i intend to update the different website sections time by time.
          <br/><br/>My best<br/> <span>Alberto</span>
        </section>
      </div>
    );
};

export default Welcome;
