import musicImage from "../../../image/music.jpg";
import styles from "./Card3.module.css";

const Card3 = () => {
  return (
    <div className={styles["container3"]}>
      <img src={musicImage} alt="sample 3" />
      <div className={styles["caption3"]}>
        <h2>Hover Style #3</h2>
        <p>
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Id facere
          distinctio facilis totam sapiente molestiae earum, velit error, atque
          a quod quibusdam unde eveniet. In rem deleniti quasi ab ut!
        </p>
        <a href="#!" alt="sample 3: hover the image!">Read More</a>
      </div>
    </div>
  );
};

export default Card3;
