import imageMusic from '../../../image/music.jpg';
import styles from './Card2.module.css';

const Card2 =()=>{
    return (
      <div style={{borderRadius:'5px'}} className={styles['container2']}>
        <img src={imageMusic} alt="sample 2" />
        <div className={styles['caption2']}>
          <h2 className={styles['test']} >Hover Style #2</h2>
          <p className={styles['p-caption2']}>
           Lorem ipsum dolor 
           sit amet consectetur adipisicing elit. Id facere distinctio facilis totam
           sapiente molestiae earum, velit error, atque a quod quibusdam unde eveniet.
           In rem deleniti quasi ab ut!
          </p>
          <a href="#!" className={styles['a-caption']}>Read More</a>
        </div>
      </div>
    );
};


export default Card2;