import { NavLink } from 'react-router-dom';
import styles from './Headbar.module.css';

const Menu=()=>{
    return (
        <div className={styles.menu}>
           <p style={{textDecoration:'none'}}>
               <NavLink  activeClassName={styles.active} to={'/buttons'}>Buttons</NavLink>
            </p>
           <p>
               <NavLink activeClassName={styles.active} to={'/cards'}>Cards</NavLink>
            </p>
           <p>
               <NavLink activeClassName={styles.active} to={'/sidebars'}>Side Bars</NavLink>
            </p>
        </div>
    );
};

export default Menu;